const sysManage_index = [
    {
        path: '/setting/sysManage',
        name: 'sysManage',
        component: resolve => require(['@/views/setting/sysManage'], resolve)
    },
    {
        path: '/setting/sysManage/sysUpdateManage',
        name: 'sysUpdateManage',
        component: resolve => require(['@/views/setting/sysManage/sysUpdateManage'], resolve)
    },
    {
        path: '/setting/sysManage/sysRoleManage',
        name: 'sysRoleManage',
        component: resolve => require(['@/views/setting/sysManage/sysRoleManage'], resolve)
    },
    {
        path: '/setting/sysManage/sysFunctionInfoManage',
        name: 'sysFunctionInfoManage',
        component: resolve => require(['@/views/setting/sysManage/sysFunctionInfoManage'], resolve)
    },
    {
        path: '/setting/sysManage/sysFunctionConf',
        name: 'sysFunctionConf',
        component: resolve => require(['@/views/setting/sysManage/sysFunctionConf'], resolve)
    },
    {
        /*系统路由信息管理*/
        path: '/setting/sysManage/sysRouterInfoManage',
        name: 'sysRouterInfoManage',
        component: resolve => require(['@/views/setting/sysManage/sysRouterInfoManage'], resolve)
    },

];
export default sysManage_index



