import axios from 'axios'
import config from './config'
import {Notification} from 'element-ui'
import router from '../router'
import store from '../store'

axios.defaults.withCredentials = true;
/* 请求异常配置 */
axios.interceptors.request.use(
    config => {
        return config;
    }, err => {
        Notification.error({
            title: '请求错误',
            message: '请求超时',
            position: 'bottom-right',
            showClose: false
        });
        return Promise.resolve(err);
    });

/* 网络请求返回去处理 */
axios.interceptors.response.use(data => {
        if (data == null) {
            Notification.error({
                title: '状态错误',
                message: '我在R.js中',
                position: 'bottom-right',
                showClose: false
            });
        }
        else {
            if (data.status === 200) {
                return data;
            }
            else {
                Notification.error({
                    title: '未发现的错误',
                    message: '请及时补充代码',
                    position: 'bottom-right',
                    showClose: false
                });
            }
        }
        return null;
    },
    err => {
        let _title = 'unknown';
        let _message = '未知错误';
        if (err.response.status === 504) {
            _title = 'Gateway Timeout';
            _message = '网关请求超时';
        }
        else if (err.response.status === 405) {
            _title = 'Method Not Allowed';
            _message = '方法不被允许';
        }
        else if (err.response.status === 404) {
            _title = 'not found';
            _message = '资源未找到';
        } else if (err.response.status === 403) {
            _title = 'Forbidden';
            _message = '没有权限访';
        }
        Notification.error({
            title: _title,
            message: _message,
            position: 'bottom-right',
            showClose: false
        });
        return Promise.resolve(err);
    }
);

class R {

    checkSession() {
        // 检查session是否存在
        let sessionInfo = window.$cookies.get('__sessionId__');
        if (sessionInfo == null) {
            store.commit('setUserInfo', {});
            store.commit('setIsLogin', false);
            router.push('/user/userLogin');
        }
    }

    get(param) {
        config.data.strSQL = param.param;
        return axios.post(param.api, {}, config);
    }

    post(data, serviceName) {
        config.data.data = data;
        config.data.serviceName = serviceName;
        // 请求之前，检查session是否存在，不存在则转跳至登录页
        if (serviceName !== 'OAMP_userRegister' && serviceName !== 'OAMP_userLogin') {
            this.checkSession();
        }
        return axios.post('/service', JSON.stringify(config.data), config);

    }

    uploadFile(formData) {
        let conf = {
            headers: {
                'Content-Type': 'multipart/form-data'
            }
        };
        // 请求之前，检查session是否存在，不存在则转跳至登录页
        this.checkSession();
        return axios.post('/OAMP/uploadFile', formData, conf)
    }
}

export default R




