<template xmlns:v-bind="http://www.w3.org/1999/xhtml">
    <el-container>
        <el-header height="40px">
            <!-- 头部 -->
            <myHeader></myHeader>
        </el-header>

        <el-container>
            <!-- 导航栏 -->
            <div>
                <myNavigation v-if="(isRouterAliveAll && isDisplayNavigation)"></myNavigation>
            </div>
            <el-container v-if="(isRouterAlive && isRouterAliveAll)">
                <!--这个是路由的内容 -->
                <!--<el-main>Main</el-main>-->
                <div class="main" :style="{height:getAutoHeight()}">
                    <router-view ></router-view>
                </div>
                <el-footer>Footer</el-footer>
            </el-container>

        </el-container>

    </el-container>
</template>

<script>
    import myNavigation from './components/common/myNavigation.vue'
    import myHeader from './components/common/myHeader.vue'

    export default {
        name: 'App',
        components: {
            myHeader,
            myNavigation,
        },
        provide() {
            return {
                reload: this.reload,
                reloadAll: this.reloadAll,
            }
        },
        data() {
            return {
                isRouterAlive: true,
                isRouterAliveAll: true,
            }
        },
        computed: {
            isDisplayNavigation: {
                get: function () {
                    return this.$store.getters.getIsDisplayNavigation
                },
                set: function () {
                }
            }
        },
        methods: {
            reload() {
                this.isRouterAlive = false;
                this.$nextTick(function () {
                    this.isRouterAlive = true;
                })
            },
            reloadAll() {
                this.isRouterAliveAll = false;
                this.$nextTick(function () {
                    this.isRouterAliveAll = true;
                })
            }
        }
    }
</script>

<style>
    .main {
        /*height: 500px;*/
        background-color: #CCCCCC;
        margin: 2px 2px;
        padding: 1px 1px !important;
        z-index: 100;
    }

    .nav {
        background-color: #fff;
        height: 38px;
        width: 100%;
        text-align: center;
        padding-top: 20px;
    }

    .el-menu-vertical-demo {
        background: #666666;
        height: 1200px;
    }

    .el-header {
        background-color: #666666;
        line-height: 40px;
        height: 40px;
        padding: 0px !important;
    }

    .el-footer {
        position: fixed;
        text-align: center;
        background-color: #666666;
        bottom: 0px;
        width: 100%;
        height: 24px !important;
        padding: 0px !important;
        z-index: 101;
    }

    .el-aside {
        background-color: #666666;
        text-align: center;
        line-height: 200px;
        width: 200px;
    }

    .el-container:nth-child(5) .el-aside,
    .el-container:nth-child(6) .el-aside {
        line-height: 260px;
    }

    .el-container:nth-child(7) .el-aside {
        line-height: 320px;
    }

</style>



