const projectManage_index = [
    {
        /*项目管理*/
        path: '/setting/projectManage',
        name: 'projectManage',
        component: resolve => require(['@/views/setting/projectManage'], resolve)
    },
    {
        /*项目信息管理*/
        path: '/setting/projectManage/projectInfoManage',
        name: 'projectInfoManage',
        component: resolve => require(['@/views/setting/projectManage/projectInfoManage'], resolve)
    },
    {
        /*项目组管理*/
        path: '/setting/projectManage/projectTeamManage',
        name: 'projectTeamManage',
        component: resolve => require(['@/views/setting/projectManage/projectTeamManage'], resolve)
    },
    {
        /*项目成员管理*/
        path: '/setting/projectManage/projectMemberManage',
        name: 'projectMemberManage',
        component: resolve => require(['@/views/setting/projectManage/projectMemberManage'], resolve)
    },
    {
        /*项目角色管理*/
        path: '/setting/projectManage/projectRoleManage',
        name: 'projectRoleManage',
        component: resolve => require(['@/views/setting/projectManage/projectRoleManage'], resolve)
    },
    {
        /*项目角色管理*/
        path: '/setting/projectManage/projectClassManage',
        name: 'projectClassManage',
        component: resolve => require(['@/views/setting/projectManage/projectClassManage'], resolve)
    },
    {
        path: '/setting/projectManage/projectClassManage/projectServiceContainerConf/:project_id',
        name: 'projectServiceContainerConf',
        component: resolve => require(['@/views/setting/projectManage/projectClassManage/projectServiceContainerConf'], resolve)
    },
    {
        path: '/setting/projectManage/projectUpdateConf',
        name: 'projectUpdateConf',
        component: resolve => require(['@/views/setting/projectManage/projectUpdateConf'], resolve)
    },
    {
        path: '/setting/projectManage/projectUpdate',
        name: 'projectUpdate',
        component: resolve => require(['@/views/setting/projectManage/projectUpdate'], resolve)
    },
];

export default projectManage_index


