const setting_index = [
    {
        path: '/setting/codeManage',
        name: 'codeManage',
        component: resolve => require(['@/views/setting/codeManage'], resolve)
    },
    {
        path: '/setting/toolManage',
        name: 'toolManage',
        component: resolve => require(['@/views/setting/toolManage'], resolve)
    },
];
export default setting_index


