const serverManage_index = [
    {
        path: '/setting/serverManage',
        name: 'serverManage',
        component: resolve => require(['@/views/setting/serverManage'], resolve)
    },
    {
        path: '/setting/serverManage/serverInfoManage',
        name: 'serverInfoManage',
        component: resolve => require(['@/views/setting/serverManage/serverInfoManage'], resolve)
    },
    {
        path: '/setting/serverManage/serverInfoManage/serviceContainerConf/:serverId',
        name: 'serviceContainerConf',
        component: resolve => require(['@/views/setting/serverManage/serverInfoManage/serviceContainerConf'], resolve)
    },
    {
        path: '/setting/serverManage/serverAgentManage',
        name: 'serverAgentManage',
        component: resolve => require(['@/views/setting/serverManage/serverAgentManage'], resolve)
    },
    {
        path: '/setting/serverManage/agentConf/:serverId',
        name: 'agentConf',
        component: resolve => require(['@/views/setting/serverManage/serverAgentManage/agentConf'], resolve)
    },

];
export default serverManage_index



