
class SocketData {


    // constructor(chatMode, msg) {
    //     this.chatMode = chatMode;
    //     this.msg = msg;
    //     this.socketData =
    //         {
    //             'chatMode': this.chatMode,
    //             'msg': this.msg
    //         }
    // }

    setChatId(chatId) {
        this.chatId = chatId;
        return this;
    }
    setSenderId(senderId) {
        this.senderId = senderId;
        return this;
    }
    /*
    GroupChat 组聊
    PersonChat 个人聊天
    */
    setChatMode(chatMode) {
        this.chatMode = chatMode;
        return this;
    }

    getChatMode() {
        return this.chatMode
    }

    setMsg(msg) {
        this.msg = msg;
        return this;
    }

    getMsg() {
        return this.msg;
    }

    getSocketData() {
        return {
            'chatMode': this.chatMode,
            'chatId': this.chatId,
            'senderId': this.senderId,
            'msg': this.msg
        }
    }
}

export default SocketData


