import Router from "vue-router";
/*记录功能*/
const record_index = [
    {
        path: '/record/mailRecord',
        name: 'mailRecord',
        component: resolve => require(['@/views/record/mailRecord'], resolve)
    },
    {
        path: '/record/flowRecord',
        name:'flowRecord',
        component: resolve => require(['@/views/record/flowRecord'], resolve)
    },
    {
        path: '/record/projectOptRecord',
        name: 'projectOptRecord',
        component: resolve => require(['@/views/record/projectOptRecord'], resolve)
    },
    {
        path: '/record/sysMessageRecord',
        name: 'sysMessageRecord',
        component: resolve => require([ '@/views/record/sysMessageRecord'], resolve)
    },
];

export default record_index


