/*菜单功能*/

const tool_index = [
    {
        path: '/tool/CDN',
        name: 'CDN',
        component: resolve => require(['@/views/tool/CDN'], resolve)
    },
    {
        path: '/tool/serviceCheck',
        name: 'serviceCheck',
        component: resolve => require(['@/views/tool/serviceCheck'], resolve)
    },
    {
        path: '/tool/generator',
        name: 'generator',
        component: resolve => require(['@/views/tool/generator'], resolve)
    },
];

export default tool_index


