const pageInfo = {
    state: {
        codeManage: {
            isShow: false,
            pageModel: 'default', /* default add view edit*/
            pageData: null
        },
        toolManage: {
            isShow: false,
            pageModel: 'default', /* default add view edit*/
            pageData: null
        },
        agentSetting: {
            isShow: false,
            pageModel: 'default', /* default add view edit*/
            pageData: null
        },
        serviceContainerConf: {
            isShow: false,
            pageModel: 'default', /* default add view edit*/
            pageData: null
        },
        projectServiceContainerConf: {
            isShow: false,
            pageModel: 'default', /* default add view edit*/
            pageData: null
        },
        serviceCheck: {
            isShow: false,
            pageModel: 'default', /* default add view edit*/
            pageData: null
        },
        userManage: {
            isShow: false,
            pageModel: 'default', /* default add view edit*/
            pageData: null
        },
        projectInfoManage: {
            isShow: false,
            pageModel: 'default', /* default add view edit*/
            pageData: null
        },
        projectTeamManage: {
            isShow: false,
            pageModel: 'default', /* default add view edit*/
            pageData: null
        },
        serverInfoManage: {
            isShow: false,
            pageModel: 'default', /* default add view edit*/
            pageData: null
        },
        serverAgentManage: {
            isShow: false,
            pageModel: 'default', /* default add view edit*/
            pageData: null
        },
        projectClassManage: {
            isShow: false,
            pageModel: 'default', /* default add view edit*/
            pageData: null
        },
        sysRoleManage: {
            isShow: false,
            pageModel: 'default', /* default add view edit*/
            pageData: null
        },
        sysRouterInfoManage: {
            isShow: false,
            pageModel: 'default', /* default add view edit*/
            pageData: null
        },
        sysRouterInfoManageNode: {
            isShow: false,
            pageModel: 'default', /* default add view edit*/
            pageData: null
        },
        sysUpdateManage: {
            isShow: false,
            pageModel: 'default', /* default add view edit*/
            pageData: null
        },
        projectRoleManage: {
            isShow: false,
            pageModel: 'default', /* default add view edit*/
            pageData: null
        },
        projectUpdateConf: {
            isShow: false,
            pageModel: 'default', /* default add view edit*/
            pageData: null
        },
        projectRemarkUpdateInfo: {
            isShow: false,
            pageModel: 'default', /* default add view edit*/
            pageData: null
        },
        projectRollBackInfo: {
            isShow: false,
            pageModel: 'default', /* default add view edit*/
            pageData: null
        },
    },
    getters: {
        getPageInfo: (state) => {
            return state;
        }
    },
    actions: {
        changePageInfo: function ({commit}, x_data) {
            commit('changePageInfo', x_data);
        }
    },
    mutations: {
        changePageInfo(state, x_data) {
            if (x_data && x_data.hasOwnProperty('pageName')) {
                state[x_data.pageName] = {};
                if (x_data.hasOwnProperty('isShow')) /*页面是否显示*/
                {
                    state[x_data.pageName]['isShow'] = x_data.isShow;
                }
                if (x_data.hasOwnProperty('pageModel')) /*页面模型*/
                {
                    state[x_data.pageName]['pageModel'] = x_data.pageModel;
                }
                if (x_data.hasOwnProperty('pageData'))/*防止双向绑定问题*/
                {
                    state[x_data.pageName]['pageData'] = JSON.parse(JSON.stringify(x_data.pageData));
                }
                if (x_data.hasOwnProperty('tableData'))/*表格数据*/
                {
                    state[x_data.pageName]['tableData'] = x_data.tableData;
                }
            } else {
                this.$message.warning('not set the right par')
            }
        }
    }
};
export default pageInfo



