const sysRouter = {
    state: {
        cur_node_info: {}
    },
    getters: {
        get_cur_node_info: state => {
            return state.cur_node_info
        },
    },
    actions: {
        change_cur_node_info: function ({commit}, vuex_data) {
            commit('change_cur_node_info', vuex_data);
        },
    },
    mutations: {
        change_cur_node_info(state, vuex_data) {
            state.cur_node_info = vuex_data;
        },
    }
};
export default sysRouter



