const runtime_index = [
    {
        path: '/runtime/runtimeAll',
        name: 'runtimeAll',
        component: resolve => require(['@/views/runtime/runtimeAll'], resolve)
    },
    {
        path: '/runtime/projectRunState',
        name: 'projectRunState',
        component: resolve => require(['@/views/runtime/projectRunState'], resolve)
    },
    {
        path: '/runtime/serverRunState',
        name: 'serverRunState',
        component: resolve => require(['@/views/runtime/serverRunState'], resolve)
    },
    {
        path: '/runtime/interfaceRunState',
        name: 'interfaceRunState',
        component: resolve => require(['@/views/runtime/interfaceRunState'], resolve)
    },
];
export default runtime_index


