export default {
    state: {
        isDisplayNavigation: true, /*是否显示左侧导航栏*/
        isCollapse:true /*左侧导航栏开合*/
    },
    getters: {
        getIsDisplayNavigation: state => {
            return state.isDisplayNavigation
        },
        getCollapse:state=>{
            return state.isCollapse
        }
    },
    actions: {
        setIsDisplayNavigation: function ({commit}, isDisplayNavigation) {
            commit('setIsDisplayNavigation', isDisplayNavigation);
        },
        changeCollapse:function ({commit}) {
            commit('changeCollapse');
        }
    },
    mutations: {
        /*设置页面是否需要登录*/
        setIsDisplayNavigation(state, isDisplayNavigation){
            state.isDisplayNavigation = isDisplayNavigation
        },
        /*改变导航栏开合状态*/
        changeCollapse (state) {
            state.isCollapse = !state.isCollapse
        }

    }
}


