/*用户管理*/
const userManage_index = [
    {
        path: '/setting/userManage',
        name: 'userManage',
        component: resolve => require(['@/views/setting/userManage'], resolve)
    },
    {
        path: '/setting/userManage/userInfoManage',
        name: 'userInfoManage',
        component: resolve => require(['@/views/setting/userManage/userInfoManage'], resolve)
    },
];

export default userManage_index


