<template>
    <el-menu default-active='home' :router="isRouter" class='el-menu-vertical-demo' @open='handleOpen' @close='handleClose' :collapse='isCollapse'>
        <div class="nav"><a href="javascrip:;" @click="m_changeCollapse" :class='[classObjCollapse]'></a></div>
        <MyTreeMenu :menuData="menu" :key="menu.router_path" v-for="menu in menus"></MyTreeMenu>
    </el-menu>
</template>


<script>
    import MyTreeMenu from './myTreeMenu.vue'
    export default {
        name: 'MyNavigation',
        components: {
            MyTreeMenu
        },
        data() {
            return {
                /*isCollapse:this.$store.getters.getCollapse,*/
                classObjCollapse: 'iconfont icon-liebiaozhankai f-szie-24',
                isRouter: true,
                menus: [
                    {
                        router_has_child: false,
                        router_has_icon: true,
                        router_icon_class: 'iconfont icon-zhuye f-szie-24',
                        router_show_name: 'home',
                        router_path: '/home',
                    },
                    {
                        router_has_child: true,
                        router_has_icon: true,
                        router_icon_class: 'iconfont icon-jiankong f-szie-24',
                        router_show_name: '运行状态',
                        router_path: '/runtime',
                        menus: [
                            {
                                router_has_child: false,
                                router_has_icon: false,
                                router_show_name: '运行总览',
                                router_path: '/runtime/runtimeAll'
                            },
                            {
                                router_has_child: false,
                                router_has_icon: false,
                                router_show_name: '项目监控',
                                router_path: '/runtime/projectRunState'
                            },
                            {
                                router_has_child: false,
                                router_has_icon: false,
                                router_show_name: '服务器监控',
                                router_path: '/runtime/serverRunState'
                            },
                            {
                                router_has_child: false,
                                router_has_icon: false,
                                router_show_name: '接口监控',
                                router_path: '/runtime/interfaceRunState'
                            }
                        ]
                    },
                    {
                        router_has_child: false,
                        router_has_icon: true,
                        router_icon_class: 'iconfont icon-iconfonticonfontjixieqimo f-szie-24',
                        router_show_name: '设置',
                        router_path: '/setting'
                    },
                    {
                        router_has_child: true,
                        router_has_icon: true,
                        router_icon_class: 'iconfont icon-gongju- f-szie-24',
                        router_show_name: '工具',
                        router_path: '/tool',
                        menus: [
                            {
                                router_has_child: false,
                                router_has_icon: false,
                                router_show_name: 'CDN',
                                router_path: '/tool/CDN'
                            },
                            {
                                router_has_child: false,
                                router_has_icon: false,
                                router_show_name: '生成器',
                                router_path: '/tool/generator'
                            },
                            {
                                router_has_child: false,
                                router_has_icon: false,
                                router_show_name: '服务检查',
                                router_path: '/tool/serviceCheck'
                            }
                        ]
                    },
                    {
                        router_has_child: true,
                        router_has_icon: true,
                        router_icon_class: 'iconfont icon-jilu f-szie-24',
                        router_show_name: '记录',
                        router_path: '/record',
                        menus: [
                            {
                                router_has_child: false,
                                router_has_icon: true,
                                router_icon_class: 'iconfont icon-liushuijilu f-szie-16',
                                router_show_name: '流水记录',
                                router_path: '/record/flowRecord'
                            },
                            {
                                router_has_child: false,
                                router_has_icon: true,
                                router_icon_class: 'iconfont icon-xiangmucaozuojilu f-szie-16',
                                router_show_name: '项目操作记录',
                                router_path: '/record/projectOptRecord'
                            }
                        ]
                    },
                    {
                        router_has_child: false,
                        router_has_icon: true,
                        router_icon_class: 'iconfont icon-banbengengxinjilu f-szie-24',
                        router_show_name: '版本更新',
                        router_name:'路由名称',
                        router_path: '/versionUpdate',
                    },
                   /* {
                        router_has_child: true,
                        router_has_icon: true,
                        router_icon_class: 'iconfont icon-banbengengxinjilu f-szie-24',
                        router_show_name: '角色管理',
                        menus: [
                            {
                                router_has_child: false,
                                router_has_icon: false,
                                router_show_name: '角色管理',
                                router_path: '/setting/sysManage/sysRoleManage'
                            },
                            {
                                router_has_child: false,
                                router_has_icon: false,
                                router_show_name: '功能管理',
                                router_path: '/setting/sysManage/sysFunctionInfoManage'
                            }
                        ]
                    }*/
                ]
            }
        },
        computed: {
            isCollapse(){
                return this.$store.getters.getCollapse
            }
        },
        methods: {
            m_changeCollapse(){
                this.$store.commit("changeCollapse")
                if (this.$store.getters.getCollapse === true)
                    this.classObjCollapse = 'iconfont icon-liebiaozhankai f-szie-24'
                else
                    this.classObjCollapse = 'iconfont icon-liebiaoshouqi f-szie-24'
            },
            handleOpen(key, keyPath) {
                //this.isCollapse = false
                // console.log(key, keyPath);
            },
            handleClose(key, keyPath) {
                //this.isCollapse = true
                // console.log(key, keyPath);
            }
        }
    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    .nav {
        background-color: #CCCCCC;
        padding-top: 20px;
        height: 38px;
        width: 100%;
        text-align: center;
    }

    .el-menu{
        background-color: #666666;
        height: 2000px;
    }
    .el-menu-vertical-demo:not(.el-menu--collapse) {
        width: 200px;
    }
</style>



