import Vue from 'vue'
import Router from 'vue-router'
import store from '../store'

import projectManage_index from './modules/setting/projectManage'
import runtime_index from './modules/runtime/runtime'
import serverManage_index from './modules/setting/serverManage'
import setting_index from './modules/setting/setting'
import sysManage_index from './modules/setting/sysManage'
import userManage_index from './modules/setting/userManage'
import tool_index from './modules/tool/tool'
import record_index from './modules/record/record'
import routerIndex from './modules/routerIndex'

Vue.use(Router);
const vueRouter = new Router({
    mode: 'history',
    base: process.env.NODE_ENV === "development" ? "" : "OAMP"
});


vueRouter.addRoutes(projectManage_index);
vueRouter.addRoutes(runtime_index);
vueRouter.addRoutes(serverManage_index);
vueRouter.addRoutes(setting_index);
vueRouter.addRoutes(sysManage_index);
vueRouter.addRoutes(userManage_index);
vueRouter.addRoutes(tool_index);
vueRouter.addRoutes(record_index);
vueRouter.addRoutes(routerIndex);

vueRouter.beforeEach(function (to, from, next) {
        store.commit('setIsDisplayNavigation', true);
        const loginInfo = store.getters.getLoginInfo;
        const noLoginRouters = ['home', 'userLogin', 'userRegistration'];
        if (to.name === '404') {
            store.commit('setIsDisplayNavigation', false);
            next();
        } else if (noLoginRouters.indexOf(to.name) >= 0) {
            if (to.name === 'userLogin' || to.name === 'userRegistration') {
                if (loginInfo.isLogin) {
                    vueRouter.push({name: 'home'})
                } else {
                    store.commit('setIsDisplayNavigation', false);
                    next();
                }
            } else {
                next()
            }
        }
        else {
            loginInfo.isLogin ? next() : vueRouter.push({name: 'userLogin'});
        }
    }
);

export default vueRouter


