<template>
    <el-submenu :index="menuData.router_path" v-if="menuData.router_has_child">

        <template v-if="menuData.router_has_icon" slot='title'>
            <i :class="menuData.router_icon_class"></i>
            <span slot='title' class="p-left-10">{{menuData.router_show_name}}</span>
        </template>
        <span v-else slot='title' class="p-left-10">{{menuData.router_show_name}}</span>

        <myTreeMenu :menuData="item" :key="item.router_path" v-for="item in menuData.menus"></myTreeMenu>
    </el-submenu>

    <el-menu-item :index="menuData.router_path" v-else>
        <i v-if="menuData.router_has_icon" :class="menuData.router_icon_class"></i>
        <span slot="title" class="p-left-10">{{menuData.router_show_name}}</span>
    </el-menu-item>
    <!--<el-menu-item :index="menuData.router_path" v-else>-->
    <!--<i v-if="menuData.router_has_icon" :class="menuData.router_icon_class"></i>-->
    <!--<span slot="title" class="p-left-10">{{menuData.router_show_name}}</span>-->
    <!--</el-menu-item>-->

</template>

<script>
    import myTreeMenu from './myTreeMenu.vue'

    export default {
        name: 'myTreeMenu',
        components: {
            myTreeMenu
        },
        props: {
            menuData: Object
        }
    }
</script>



