const authority = {
    state: {
        project_team_id: JSON.parse(sessionStorage.getItem('project_team_id')),/*用户选择项目id*/
        project_id: JSON.parse(sessionStorage.getItem('project_id')),/*用户选择项目id*/
        structure: {}
    },
    getters: {
        get_authority_project_team_id: state => {
            return state.project_team_id
        },
        get_authority_project_id: state => {
            return state.project_id
        },
        get_authority_structure: state => {
            return state.structure
        },
    },
    actions: {
        change_authority_project_team_id: function ({commit}, vuex_data) {
            commit('change_authority_project_team_id', vuex_data);
        },
        change_authority_project_id: function ({commit}, vuex_data) {
            commit('change_authority_project_id', vuex_data);
        },
        change_authority_structure: function ({commit}, vuex_data) {
            commit('change_authority_structure', vuex_data);
        },
    },
    mutations: {
        change_authority_project_team_id(state, vuex_data) {
            sessionStorage.setItem('project_team_id', JSON.stringify(vuex_data));
            state.project_team_id = vuex_data;
        },
        change_authority_project_id(state, vuex_data) {
            sessionStorage.setItem('project_id', JSON.stringify(vuex_data));
            state.project_id = vuex_data;
        },
        change_authority_structure(state, vuex_data) {
            state.structure = vuex_data;
        },
    }
};
export default authority



