const codeSetting = {
    state: {
        codeInfoPage: {
            isShow: false,
            pageModel: 'view', /*add view edit*/
            pageData: null
        },
        codeTable: []
    },
    getters: {
        getCodeInfoPage: state => {
            return state.codeInfoPage
        },
        getCodeTable: state => {
            return state.codeTable
        }
    },
    actions: {
        changeCodeInfoPage: function ({commit}, vuex_data) {
            commit('changeCodeInfoPage', vuex_data);
        },
        setCodeTable: function ({commit}, data) {
            commit('setCodeTable', data);
        }
    },
    mutations: {
        changeCodeInfoPage (state, vuex_data) {
            state.codeInfoPage.isShow = !state.codeInfoPage.isShow;
            if (vuex_data) {
                if (vuex_data.hasOwnProperty('isShow'))
                    state.codeInfoPage.isShow = vuex_data.isShow;
                if (vuex_data.hasOwnProperty('pageModel'))
                    state.codeInfoPage.pageModel = vuex_data.pageModel;
                if (vuex_data.hasOwnProperty('pageData')) {
                    /*防止双向绑定问题*/
                    state.codeInfoPage.pageData = JSON.parse(JSON.stringify(vuex_data.pageData));
                }
            }
        },
        /*设置列表页数据*/
        setCodeTable(state, data){
            state.codeTable = data
        }
    }
}
export default codeSetting



