import Vue from 'vue';

import ElementUI from 'element-ui';
import 'element-ui/lib/theme-chalk/index.css';

Vue.use(ElementUI);

import App from './App.vue';

import router from './router'
import '../static/css/main.css'
import store from './store'
import Base from './utils/base'
import uploader from 'vue-simple-uploader'
import VueCookies from 'vue-cookies'
import R from './http/R.js'
import SocketData from './utils/SocketData.js'
import ENUM from './utils/enum.js'
// import config from "./http/config";
// import config_conf from "../config"


Vue.config.productionTip = false;


Vue.use(Base);
Vue.use(uploader);
Vue.use(VueCookies);
Vue.prototype.$http = R;
Vue.prototype.$store = store;
Vue.prototype.$enum = ENUM;

Vue.prototype.$createSocketData = function (cahtMode,chatId,senderId, msg) {
    return new SocketData().setChatMode(cahtMode).setChatId(chatId).setSenderId(senderId).setMsg(msg).getSocketData()
};

/*2018年12月19日18:10:24 添加预制参数*/
Vue.prototype.onAdd = function (moduleName,) {
    store.commit('changePageInfo', {pageName: moduleName, isShow: true, pageModel: 'add', pageData: null});
};
Vue.prototype.onView = function (moduleName, index, rows) {
    store.commit('changePageInfo', {pageName: moduleName, isShow: true, pageModel: 'view', pageData: rows[index]});
};
Vue.prototype.onEdit = function (moduleName, index, rows) {
    store.commit('changePageInfo', {pageName: moduleName, isShow: true, pageModel: 'edit', pageData: rows[index]});
};

Vue.prototype.onClose = function (moduleName, ref, refreshPageMethod) // 关闭对话框
{
    if (ref) {
        if (typeof(ref) === 'string')
            this.$refs[ref].resetFields();
        else if (ref instanceof Array)
            for (let i = 0; i < ref.length; i++) {
                this.$refs[ref[i]].resetFields();
            }
        else
            return;
    } else {
        /*refreshPageMethod 为空 ， 如 列表页面*/
    }
    if (moduleName)
        store.commit('changePageInfo', {pageName: moduleName, isShow: false, pageModel: 'default', pageData: null});
    if (refreshPageMethod)
        store.state.Apps.$emit(refreshPageMethod);
};

Vue.prototype.onIndexMethod = function (pageNum = 1, pageSize = 1) {
    return (pageNum - 1) * pageSize + 1;
};
Vue.prototype.getTableHeight = function (search_height) {
    return ((document.documentElement.offsetHeight || document.body.offsetHeight) - 78 - search_height)
};

Vue.prototype.getAutoWidth = function (diff_width = 0) {
    return ((document.documentElement.clientWidth || document.body.clientWidth) - diff_width) + 'px'

};

Vue.prototype.getAutoHeight = function (diff_height = 0) {
    return ((document.documentElement.offsetHeight || document.body.offsetHeight) - diff_height) + 'px'
};

Vue.prototype.$onClose = function (moduleName, ref, refreshPageMethod) // 关闭对话框
{

    if (ref)
        this.$refs[ref].resetFields();

    store.commit('changePageInfo', {pageName: moduleName, isShow: false, pageModel: 'default', pageData: null});
    if (refreshPageMethod)
        store.state.Apps.$emit(refreshPageMethod);
};
Vue.prototype.$emit_method = function (refreshPageMethod) {
    if (refreshPageMethod)
        store.state.Apps.$emit(refreshPageMethod);
};
Vue.prototype.$insert = async function (item, methodName) {
    let R = new this.$http();
    let {data} = await R.post(item, methodName);
    if (data != null && data !== 'null')
        return data.data;
    else {
        this.$message.warning('insert data not return suitable value')
        return null;
    }
};
Vue.prototype.$del = async function (item, methodName) {
    let R = new this.$http();
    let {data} = await R.post(item, methodName);
    return data !== null && data !== 'null'
};
Vue.prototype.$update = async function (item, methodName) {
    let R = new this.$http();
    let {data} = await R.post(item, methodName);
    if (data != null && data !== 'null')
        return data;
    else {
        this.$message.warning('update data not return suitable value');
        return null;
    }
};
console.log(process.env.NODE_ENV);
Vue.prototype.$socketURI = process.env.NODE_ENV === "development" ? "ws://127.0.0.1:8007/OAMP/socketService" : "ws://127.0.0.1:8080/OAMP/socketService";
Vue.prototype.$socketCHATURI = process.env.NODE_ENV === "development" ? "ws://127.0.0.1:8007/OAMP/chat" : "ws://127.0.0.1:8080/OAMP/chat";
Vue.prototype.$exec = async function (item, methodName) {
    let R = new this.$http();
    let {data} = await R.post(item, methodName);
    return data;
};

Vue.prototype.$search = async function (item, methodName) {
    let R = new this.$http();
    let {data} = await R.post(item, methodName);
    return data;
};

Vue.prototype.$getCodeValuesByCode = async function (code) {
    let _this = this;
    let R = new _this.$http();
    let {data} = await R.post({'code': code}, 'OAMP_search_code_data_list');
    return data.data;
};
// 获取项目信息 项目组名称 项目组id 项目组
Vue.prototype.$getProjectTeamCodeInfo = async function (item = {}) {
    let R = new this.$http();
    let {data} = await R.post(item, 'OAMP_search_project_team_code_list');
    return data.data;
};
// 获取项目信息 项目名称 项目id 项目
Vue.prototype.$getProjectCodeInfo = async function (item = {}) {
    let R = new this.$http();
    let {data} = await R.post(item, 'OAMP_search_project_code_list');
    return data.data;
};
Vue.prototype.$setPageInfo = function (moduleName) // 设置pageInfo页面
{
    let x_pageInfo = store.getters.getPageInfo[moduleName];
    if (x_pageInfo.pageModel === 'add' || x_pageInfo.pageModel === 'default')
        this.pageData = {};
    else {
        this.pageData = x_pageInfo.pageData;
    }
    return x_pageInfo;
};
Vue.prototype.$setCodeList = async function (codeList, code) // 设置pageInfo页面
{
    if (!codeList) {
        return await this.$getCodeValuesByCode(code);
    }
    // if (codeList.length <= 0) {
    //     return await this.$getCodeValuesByCode(code);
    // }
    return null
};
Vue.prototype.$formatCodeValue = function (row, codeList, key, itemKeyName = 'codeId', itemValueName = 'codeValue') {
    if (codeList && codeList.length > 0) {
        let res = codeList.filter(item => item[itemKeyName] === row[key]);
        if (res != null && res.length > 0) {
            return res[0][itemValueName];
        }
    }
    return ''
};

Vue.prototype.$format_bool = function (row, column, cellValue) {
    return row[column.property] === "true" ? '是' : '否';
};
new Vue({
    el: '#app',
    store,
    router,
    components: {App},
    template: '<App/>'
    // render: h => h(App)
});


