function isInteger(obj) {
    try {
        return obj % 1 === 0; //是整数，则返回true，否则返回false
    }
    catch (e) {
        return false;
    }
}
function isIP(obj) {
    var exp = /^(\d{1,2}|1\d\d|2[0-4]\d|25[0-5])\.(\d{1,2}|1\d\d|2[0-4]\d|25[0-5])\.(\d{1,2}|1\d\d|2[0-4]\d|25[0-5])\.(\d{1,2}|1\d\d|2[0-4]\d|25[0-5])$/;
    return null == obj.match(exp);
}

exports.install = function (Vue, options) {
    Vue.prototype.$checkPort = (rule, value, callback) => {
        if (value != null && value != "") {
            if (!isInteger(value)) {
                callback(new Error('请输如正整数!'))
            } else {
                if (value < 0) {
                    callback(new Error('输入端口值小于端口规定值0'))
                }
                if (value > 65535) {
                    callback(new Error('输入端口值大于端口规定值65535'))
                } else {
                    callback()
                }
            }
        }
        else {
            callback()
        }
    };
    Vue.prototype.$checkIP = (rule, value, callback) => {
        if (value != null && value != "") {
            if (isIP(value)) {
                callback(new Error('输入ip有误，请重新输入'))
            } else {
                callback()
            }
        }
        else {
            callback()
        }
    };
};



