<template>
    <el-row :gutter="20">
        <el-col :span="2">
            <div class="grid-content">
                <div class="icon">
                    <span class="v-href" @click="toHome">OAMP</span>
                </div>
            </div>
        </el-col>
        <el-col :span="22">
            <el-col :span="20" :xl="20" :lg="20" :md="17" :sm="15" :xs="12">
                <el-col :xl="16" :lg="16" :md="14" :sm="12" :xs="10">
                    <div style="color: #666666">|</div>
                </el-col>
                <el-col v-if="loginInfo.isLogin===true" :xl="8" :lg="8" :md="10" :sm="12" :xs="14">
                    <el-col :span="12">
                        <el-select style="width: 100%;" size="mini"
                                   v-model="project_team_id"
                                   placeholder="请选择项目组"
                                   clearable
                                   @change="project_team_change"
                        >
                            <el-option v-for="item in project_team_code_list"
                                       :key="item._id"
                                       :label="item.project_team_name"
                                       :value="item._id">
                            </el-option>
                        </el-select>
                    </el-col>
                    <el-col :span="12">
                        <el-select style="width: 95%;padding-left: 15px;"
                                   size="mini"
                                   v-model="project_id"
                                   placeholder="请选择当前项目"
                                   clearable
                                   @change="project_change"
                        >
                            <el-option v-for="item in project_code_list"
                                       :key="item._id"
                                       :label="item.project_name"
                                       :value="item._id">
                            </el-option>
                        </el-select>
                    </el-col>
                </el-col>
            </el-col>
            <el-col :span="4" :xl="4" :lg="4" :md="7" :sm="9" :xs="12">
                <el-col :span="4">
                    <div class="message-box">
                        <router-link v-if="loginInfo.isLogin" :to="'/user/userMessage'">
                            <i class="iconfont icon-mail f-szie-32"></i>
                        </router-link>
                    </div>
                </el-col>
                <el-col :offset="1" :span="4">
                    <div class="login-header">
                        <router-link :to="'/user/userInfo'">
                            <i class="iconfont icon-yonghu f-szie-24"></i>
                        </router-link>
                    </div>
                </el-col>
                <el-col :offset="1" :span="14">
                    <el-dropdown class="login" v-if="loginInfo.isLogin" @command="handleCommand">
                        <span class="el-dropdown-link">{{loginInfo.userInfo.nickName}}</span>
                        <el-dropdown-menu slot="dropdown">
                            <el-dropdown-item command="userInfo">个人信息</el-dropdown-item>
                            <el-dropdown-item command="userSetting">个人设置</el-dropdown-item>
                            <el-dropdown-item command="userQuit">退出</el-dropdown-item>
                        </el-dropdown-menu>
                    </el-dropdown>
                    <div class="logout" v-else>
                        <!--<a><span class="v-href" @click="toLogin">登录</span>/<span class="v-href" @click="toRegin">注册</span></a>-->
                        <a><span class="v-href" @click="toLogin">登录</span></a>
                    </div>
                </el-col>
            </el-col>
        </el-col>
    </el-row>
</template>

<script>
    export default {
        name: 'myHeader',
        inject: ['reload'],
        data() {
            return {
                project_team_code_list: [],
                project_code_list: [],
                project_id: this.$store.getters.get_authority_project_id,
                project_team_id: this.$store.getters.get_authority_project_team_id,
            }
        },
        computed: {
            loginInfo: function () {
                return this.$store.getters.getLoginInfo
            }
        },
        async mounted() {
            if (this.$store.getters.getIsLogin) {
                this.project_team_code_list = await this.$getProjectTeamCodeInfo();
                this.project_code_list = await this.$getProjectCodeInfo({'project_team_id': this.project_team_id});
            }
        },
        methods: {
            async project_team_change(project_team_id)/*项目组选择变更*/ {
                this.$store.commit('change_authority_project_team_id', project_team_id);
                this.$store.commit('change_authority_project_id', '');
                this.project_id = '';
                this.project_code_list = await this.$getProjectCodeInfo({'project_team_id': project_team_id});
            },
            project_change(project_id)/*项目选择变更*/ {
                /*1、重载权限*/
                /*2、重载页面*/
                this.$store.commit('change_authority_project_id', project_id);
                // this.$router.push( {name: 'home'});
                // 通过权限判断是否可以直接在本页面reload to fixed 2019年1月15日09:13:28
                this.reload();
            },
            handleCommand(command) {
                let _this = this;
                if (command === 'userInfo') {
                    this.$router.push('/user/userInfo')
                }
                if (command === 'userSetting') {
                    this.$router.push('/user/userSetting')
                }
                if (command === 'userQuit') {
                    let R = new this.$http();
                    R.post({}, 'OAMP_userQuit').then(async ({data}) => {
                        if (await data.isQuit) {
                            _this.$message.success('退出成功')
                        }
                        else {
                            _this.$message.warning('退出成功，session 清理失败，请检查代码')
                        }
                        /* 设置登录信息*/
                        _this.$store.commit('setLoginInfo', {});
                        _this.$store.commit('setIsLogin', false);
                        /*清空sessionStorage信息*/
                        _this.$cookies.remove('__sessionId__');
                        location.reload();
                    });
                }
            },
            toLogin() {
                this.$router.push('/user/userLogin')
            },
            toRegin() {
                this.$router.push('/user/userRegistration')
            },
            toHome() {
                this.$router.push('/home')
            }
        }
    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    .message-box {
        margin-top: 6px;
        text-align: center;
    }

    .login-header {
        margin-top: 6px;
        text-align: center;
        height: 100%;
        width: 40px;
        border-radius: 20px;
        background-color: #ccc;
    }

    .logout {
        margin-top: 5px;
    }

    .login {
        margin-top: 5px;
    }

    .el-dropdown-link {
        cursor: pointer;
        font-size: 16px;
        color: #fff;
        background-color: #666666;
    }

    .grid-content {
        padding-left: 0px;
        border-radius: 4px;
        height: 100%;
    }

    .icon {
        padding-top: 2px;
        padding-left: 10px;
        font-size: 20px;
        text-align: left;
    }
</style>



