const routerIndex = [
    {
        path: '/',
        name: 'home',
        redirect: 'home'
    },
    {
        path: '/index',
        name: 'home',
        redirect: 'home'
    },
    {
        path: '/home',
        name: 'home',
        component: resolve => require(['@/views/home'], resolve)
    },
    {
        path: '/setting',
        name: 'setting',
        component: resolve => require(['@/views/setting'], resolve)
    },
    // {
    //     path: '/record',
    //     name: 'record',
    //     component: resolve => require(['@/views/record'], resolve)
    // },
    // {
    //     path: '/runtime',
    //     name: 'runtime',
    //     component: resolve => require(['@/views/runtime'], resolve)
    // },
    /*用户相关操作*/
    // {
    //     path: '/user/userRegistration',
    //     name: 'userRegistration',
    //     //component: userRegistration
    //     component: resolve => require([''@/views/user/userRegistration'], resolve)
    // },
    {
        path: '/user/userLogin',
        name: 'userLogin',
        component: resolve => require(['@/views/user/userLogin'], resolve)
    },
    {
        path: '/user/userMessage',
        name: 'userMessage',
        component: resolve => require(['@/views/user/userMessage'], resolve)
    },
    {
        path: '/versionUpdate',
        name: 'versionUpdate',
        component: resolve => require(['@/views/versionUpdate'], resolve)
    },
    {
        path: '/*',
        name: '404',
        component: resolve => require(['@/views/404'], resolve)
    }
];

export default routerIndex


