export default {
    state: {
        loginInfo:{
            isLogin:JSON.parse(sessionStorage.getItem('isLogin')),/*用户是否登录*/
            userInfo:JSON.parse(sessionStorage.getItem('userInfo'))/*用户信息*/
        }
    },
    getters: {
        getLoginInfo: state => {
            return state.loginInfo
        },
        getIsLogin: state => {
            return state.loginInfo.isLogin
        }
    },
    actions: {
        setLoginInfo: function ({commit}, data) {
            commit('setLoginInfo', data);
        },
        setUserInfo: function ({commit}, userInfo) {
            commit('setUserInfo', userInfo);
        },
        setIsLogin: function ({commit}, isLogin) {
            commit('setIsLogin', isLogin);
        }
    },
    mutations: {
        /*设置登录信息*/
        setLoginInfo(state, data){
            state.loginInfo = data
        },
        /*设置用户信息*/
        setUserInfo(state, userInfo){
            sessionStorage.setItem('userInfo', JSON.stringify(userInfo));
            state.loginInfo.userInfo = userInfo
        },
        /*设置是否登录状态*/
        setIsLogin(state, isLogin){
            sessionStorage.setItem('isLogin', JSON.stringify(isLogin));
            state.loginInfo.isLogin = isLogin
        }
    }
}


